.app {
  display: flex;
  background-color: white;
  height: 90vh; }

.sidebar{
    display: flex;
    flex-direction: column;
    flex:0.25 1
}

.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10;
}

.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_searchContainer > input {
    border: none;
    margin-left: 10px;
}

.sidebar_header{
    display: flex;
    justify-content: space-between;
    border-right: 1px solid lightgray;
}

.sidebar_headerRight{
    display: flex;
    align-items: center;
}

.sidebar_chats{
    flex: 1 1;
    overflow: scroll;
    background-color: whitesmoke;
}

.sidebarChat{
    display: flex;
    padding: 8px;
    cursor: pointer;
    border-bottom: 1px solid rgb(218, 212, 212);
}

.sidebarChat:hover{
    background-color:#ebebeb;
}

.sidebar_chat_info > h2{
    font-size: 16px;
    margin-bottom: 8px;
    font-weight: bold;
}

.sidebar_chat_info{
    margin-left: 15px;
}
.chat{
    flex:0.75 1;
    display: flex;
    flex-direction: column;
}

.chat_header{
    display: flex;
    padding: 10px;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.chat_headerInfo > h6{
    margin-bottom: 2px;
    font-size: 500;
    font-weight: bold;
}

.chat_body{
    flex:1 1;
    background-color: gainsboro;
    overflow-y: scroll;
    padding: 30px 30px 0px 30px
}

.chat_message{
    position: relative;
    font-size: 16px;
    padding: 10px 10px 0px 10px;
    background-color: white;
    border-radius: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 30px;
}

.chat_timeStamp{
    margin-left: 10px;
    font-size: xx-small;
}

.chat_reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid gray;
}

.chat_footer > form{
    flex: 1 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1 1;
    border-radius: 30px;
}
